import React from "react";
import Number from "./Number";
import VisibilitySensor from 'react-visibility-sensor';

function Numbers(props) {
    return (
        /* TITULO */
        <div class="container text-center">
            <div class="row tit-cstm">
                <div class="col"></div>
                <div class="col-6">
                    <div class="text-center">
                        <h1 class="fonts cstm-sora-md fs-1">
                            {props.title}
                        </h1>
                    </div>
                </div>
                <div class="col"></div>
            </div>

            {/* BODY */}
            <div class="container">
                <div class="row cstm">
                    {/*12.5% IZQUIERDO*/}
                    <div class="col"></div>

                    <div class="col-10">
                        <div class="row row-cols-1 row-cols-md-3 g-4">
                            <div class="col">
                                <div class="number cstm">
                                    <VisibilitySensor >
                                        {({ isVisible }) => (
                                            <p>{isVisible ? <Number end={70} /> : "0%"}</p>
                                        )}
                                    </VisibilitySensor>
                                </div>
                                <div class="card-title cstm fs-5">
                                    Reduccin de costos
                                </div>
                            </div>
                            <div class="col">
                                <div class="number cstm">
                                    <VisibilitySensor>
                                        {({ isVisible }) => (
                                            <p>{isVisible ? <Number end={90} /> : "0%"}</p>
                                        )}
                                    </VisibilitySensor>
                                </div>
                                <div class="card-title cstm fs-5">
                                    Reduccin de procesos manuales
                                </div>
                            </div>
                            <div class="col">
                                <div class="number cstm">
                                    <VisibilitySensor>
                                        {({ isVisible }) => (
                                            <p>{isVisible ? <Number end={65} /> : "0%"}</p>
                                        )}
                                    </VisibilitySensor>
                                </div>
                                <div class="card-title cstm fs-5">
                                    Aumento de productividad
                                </div>
                            </div>
                        </div>
                    </div>
                    {/*12.5% DERECHO*/}
                    <div class="col"></div>
                </div>
            </div>
        </div >
    );
}

export default Numbers;